import { CHART_COLORS, transparentize } from "./chart.utils";
import Chart from "chart.js/auto";
import "./styles.css";

const labels = [
  "Team",
  "Funding",
  "Market",
  "Traction",
  "Growth",
  "Technology"
];

const searchParams = new URL(document.location).searchParams;
const values = [];
labels
  .map((v) => v.toLowerCase())
  .forEach((key) => {
    const paramValue = searchParams.get(key);
    values.push(paramValue || 0);
  });

const data = {
  labels: labels,
  datasets: [
    {
      label: "Dataset 1",
      data: values,
      borderColor: CHART_COLORS.red,
      backgroundColor: transparentize(CHART_COLORS.red, 0.5)
    }
  ]
};

const config = {
  type: "radar",
  data: data,
  options: {
    responsive: true,
    plugins: {
      legend: {
        display: false
      }
    },
    scales: {
      r: {
        angleLines: {
          display: false
        },
        suggestedMin: 0,
        suggestedMax: 10
      }
    }
  }
};

const myChart = new Chart(document.getElementById("myChart"), config);
